	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>OpenStack Special Interest Group</h1>\n<p>Coordinates the cross-community efforts of the OpenStack and Kubernetes communities. This includes OpenStack-related contributions to Kubernetes projects with OpenStack as: a deployment platform for Kubernetes; a service provider for Kubernetes; a collection of applications to run on Kubernetes.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/j/417251241\">Wednesdays at 16:00 PT (Pacific Time)</a> (biweekly). <a href=\"http://www.thetimezoneconverter.com/?t=16:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1iAQ3LSF_Ky6uZdFtEZPD_8i6HXeFxIeW4XtGcUJtPyU/edit?usp=sharing_eixpa_nl&#x26;ts=588b986f\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/watch?v=iCfUx7ilh0E&#x26;list=PL69nYSiGNLP20iTSChQ_i2QQmTBl3M7ax\">Meeting recordings</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Chris Hoge (<strong><a href=\"https://github.com/hogepodge\">@hogepodge</a></strong>), OpenStack Foundation</li>\n<li>David Lyle (<strong><a href=\"https://github.com/dklyle\">@dklyle</a></strong>), Intel</li>\n<li>Robert Morse (<strong><a href=\"https://github.com/rjmorse\">@rjmorse</a></strong>), Ticketmaster</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-openstack\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Fopenstack\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-openstack:</p>\n<ul>\n<li>\n<p><strong>cloud-provider-openstack</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/cloud-provider-openstack/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/cloud-provider-openstack/master/OWNERS</a></li>\n</ul>\n</li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-openstack-api-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-openstack-api-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack-api-reviews\">link</a></td>\n<td>API Changes and Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-openstack-bugs</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-openstack-bugs\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack-bugs\">link</a></td>\n<td>Bug Triage and Troubleshooting</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-openstack-feature-requests</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-openstack-feature-requests\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack-feature-requests\">link</a></td>\n<td>Feature Requests</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-openstack-misc</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-openstack-misc\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack-misc\">link</a></td>\n<td>General Discussion</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-openstack-pr-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-openstack-pr-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack-pr-reviews\">link</a></td>\n<td>PR Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-openstack-proposals</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-openstack-proposals\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack-proposals\">link</a></td>\n<td>Design Proposals</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-openstack-test-failures</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-openstack-test-failures\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-openstack-test-failures\">link</a></td>\n<td>Test Failures and Triage</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<!-- END CUSTOM CONTENT -->"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-openstack/"}}

